const hastag = (str, b = true, size = '1x') => {

    const outputStr = (b === true) ? <b>{str}</b> : str;

    return (
        <>
            <Icons icon="faHashtag" size={size} />
            {outputStr}
            &nbsp;
        </>
    );

};

