import React from 'react';
import PropTypes from 'prop-types';
import Icons from './Icons';
import {
    SOCIAL_URL_GITHUB,
    SOCIAL_URL_INSTAGRAM,
    SOCIAL_URL_LINKEDIN,
    SOCIAL_URL_TWITTER,
    SOCIAL_URL_MAIL
} from '../../config/cons';

function SocialLinks ({ type, size }) {
    let icon;
    let url;

    switch (type) {
        case 'github':
            icon = 'faGithub';
            url = SOCIAL_URL_GITHUB;
            break;

        case 'instagram':
            icon = 'faInstagram';
            url = SOCIAL_URL_INSTAGRAM;
            break;

        case 'linkedin':
            icon = 'faLinkedin';
            url = SOCIAL_URL_LINKEDIN;
            break;

        case 'twitter':
            icon = 'faTwitter';
            url = SOCIAL_URL_TWITTER;
            break;

        case 'mail':
            icon = 'faEnvelope';
            url = SOCIAL_URL_MAIL;
            break;

        default:
            break;
    }
    return (
        <>
            <a
                href={url}
                target="_blank"
                rel="noreferrer"
                className="icon-links"
            >
                <Icons icon={icon} size={size} />
            </a>
        </>
    );
}

SocialLinks.propTypes = {
    type: PropTypes.string.isRequired,
    size: PropTypes.string.isRequired
};

export default SocialLinks;
